import store from "../store/index";
import axios from "axios";
import jwt from "jsonwebtoken";
import router from "../router";

const authenticate = function() {
  if (
    store.state.authentication.currentUser &&
    Date.now() / 1000 >= store.state.authentication.currentUser.exp
  ) {
    let API = "http://127.0.0.1:3000/api";
    axios
      .post(API + "/refresh_token", {}, { withCredentials: true })
      .then(response => {
        let token = response.data.accessToken;
        const user = jwt.decode(token);
        store.dispatch("resetCredentials", { user, token });
      });
  }

  if (
    !store.state.authentication.currentUser &&
    Date.now() >= store.state.authentication.currentUser.exp
  ) {
    router.push("/login");
  }
};

const silentRefresh = function() {
  return new Promise((resolve, reject) => {
    if (!store.state.authentication.currentUser) {
      let API = "http://127.0.0.1:3000/api";
      axios
        .post(API + "/refresh-token", {}, { withCredentials: true })
        .then(response => {
          let token = response.data.accessToken;
          const user = jwt.decode(token);
          store.dispatch("resetCredentials", { user, token });
          resolve();
        })
        .catch(() => {
          reject();
        });
    }
  });
};

export { authenticate, silentRefresh };
