import axios from "axios";

const API = "http://127.0.0.1:3000/api";

const partner = {
  state: {
    labRequests: [],
    isCreateTestModalOn: false,
    activeLabRequest: null,
    isLabRequestModalOn: false
  },
  mutations: {
    UPDATE_LAB_REQUESTS(state, labRequests) {
      state.labRequests = labRequests;
    },
    TOGGLE_CREATE_TEST_MODAL(state, status) {
      state.isCreateTestModalOn = status;
    },
    ADD_LAB_REQUEST(state, labRequest) {
      state.labRequests.push(labRequest);
    },
    TOGGLE_LAB_REQUEST_MODAL(state, status) {
      state.isLabRequestModalOn = status;
    },
    UPDATE_ACTIVE_LAB_REQUEST(state, labRequest) {
      state.activeLabRequest = labRequest;
    }
  },
  actions: {
    toggleCreatePartnerTestModalOn(context, status) {
      context.commit("TOGGLE_CREATE_TEST_MODAL", status);
    },
    fetchPartnerLabRequests(context, { token, partnerlab }) {
      axios
        .get(API + "/labrequests", {
          params: {
            partnerlab: partnerlab.name
          },
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_LAB_REQUESTS", response.data);
        })
        .catch(() => context.commit("UPDATE_LAB_REQUESTS", []));
    },
    addLabRequest(context, labRequest) {
      context.commit("ADD_LAB_REQUEST", labRequest);
    },
    togglePartnerLabRequestModal(context, status) {
      context.commit("TOGGLE_LAB_REQUEST_MODAL", status);
    },
    updatePartnerActiveLabRequest(context, labRequest) {
      context.commit("UPDATE_ACTIVE_LAB_REQUEST", labRequest);
    }
  }
};

export default partner;
