import axios from "axios";

const API = "http://127.0.0.1:3000/api";

const biologist = {
  state: {
    labRequests: [],
    selectedVerificationExamReqeust: null,
    isResultVerificationModalOn: false
  },
  mutations: {
    UPDATE_LAB_REQUESTS(state, labRequests) {
      state.labRequests = labRequests;
    },
    UPDATE_SELECTED_VERIFICATION_EXAM_REQUEST(state, status) {
      state.selectedVerificationExamReqeust = status;
    },
    TOGGLE_RESULT_VERIFICATION_MODAL(state, status) {
      state.isResultVerificationModalOn = status;
    }
  },
  actions: {
    fetchBiologistLabRequests(context, token) {
      axios
        .get(API + "/labrequests?status=result_registered", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_LAB_REQUESTS", response.data);
        });
    },
    updateSelectedVerificationExamRequest(context, labRequest) {
      context.commit("UPDATE_SELECTED_VERIFICATION_EXAM_REQUEST", labRequest);
    },
    toggleResultVerificationModalOn(context, status) {
      context.commit("TOGGLE_RESULT_VERIFICATION_MODAL", status);
    }
  },
  getters: {
    getPaidExamRequests(state) {
      return state.labRequests.filter(labRequest => {
        return labRequest.status === "result_registered";
      });
    }
  }
};

export default biologist;
