import axios from "axios";

const API = "http://127.0.0.1:3000/api";

const receptionist = {
  state: {
    patients: [],
    activePatient: null,
    vaccineActivePatient: null,
    isCreatePatientModalOn: false,
    isCreateTestModalOn: false,
    isCreateVaccineModalOn: false,
    exams: [],
    vaccines: [],
    results: []
  },
  mutations: {
    TOGGLE_CREATE_PATIENT_MODAL(state, status) {
      state.isCreatePatientModalOn = status;
    },
    TOGGLE_CREATE_TEST_MODAL(state, status) {
      state.isCreateTestModalOn = status;
    },
    TOGGLE_CREATE_VACCINE_MODAL(state, status) {
      state.isCreateVaccineModalOn = status;
    },
    UPDATE_EXAMS(state, exams) {
      state.exams = exams;
    },
    UPDATE_VACCINES(state, vaccines) {
      state.vaccines = vaccines;
    },
    UPDATE_PATIENTS(state, patients) {
      state.patients = patients;
    },
    UPDATE_ACTIVE_PATIENT(state, patient) {
      state.activePatient = patient;
    },
    UPDATE_VACCINE_ACTIVE_PATIENT(state, patient) {
      state.vaccineActivePatient = patient;
    },
    ADD_PATIENT(state, patient) {
      state.patients.push(patient);
    },
    UPDATE_RESULTS(state, results) {
      state.results = results;
    }
  },
  actions: {
    toggleCreatePatientModalOn(context, status) {
      context.commit("TOGGLE_CREATE_PATIENT_MODAL", status);
    },
    toggleCreateTestModalOn(context, status) {
      context.commit("TOGGLE_CREATE_TEST_MODAL", status);
    },
    toggleCreateVaccineModalOn(context, status) {
      context.commit("TOGGLE_CREATE_VACCINE_MODAL", status);
    },
    updateActivePatient(context, patient) {
      context.commit("UPDATE_ACTIVE_PATIENT", patient);
    },
    updateVaccineActivePatient(context, patient) {
      context.commit("UPDATE_VACCINE_ACTIVE_PATIENT", patient);
    },
    fetchExams(context, token) {
      axios
        .get(API + "/exams", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_EXAMS", response.data);
        });
    },
    fetchVaccines(context, token) {
      axios
        .get(API + "/vaccines", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_VACCINES", response.data);
        });
    },
    fetchPatients(context, token) {
      axios
        .get(API + "/patients", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_PATIENTS", response.data);
        });
    },
    addNewPatient(context, patient) {
      context.commit("ADD_PATIENT", patient);
    },
    createVaccineRequest(context, labRequest) {
      labRequest.status = "created";
      labRequest.total_cost = labRequest.vaccines.reduce(
        (cost, vaccine) => (cost += vaccine.cost),
        0
      );
      labRequest.exams = [];
      labRequest.order_time = Date.now();
      labRequest.created_at = Date.now();
      labRequest.updated_at = Date.now();
      context.commit("UPDATE_LAB_REQUEST", labRequest);
    },
    fetchResults(context, token) {
      axios
        .get(API + "/labrequests?status=verified", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_RESULTS", response.data);
        });
    }
  },
  getters: {
    examsToBeOrdered(state) {
      return state.exams.map(exam => {
        return { label: exam.name, exam: exam };
      });
    },
    vaccinesToBeOrdered(state) {
      return state.vaccines.map(vaccine => {
        return { label: vaccine.name, vaccine: vaccine };
      });
    }
  }
};

export default receptionist;
