<template>
    <section class="">
        <div class="container">
            <div class="columns is-centered">
                <div class="column is-5-tablet is-4-desktop is-4-widescreen">
                    <h1 class="title has-text-centered has-text-link" style="padding-top: 3em;">Healthstack</h1>
                    <div class="box">
                        <div class="has-text-centered" style="margin-bottom: 1em;">
                            <p><Strong>Welcome back!</Strong></p>
                            <p
                                v-if="loginError"
                                style="margin-top: 1em;" 
                                class="has-text-danger is-size-7">
                                    {{ loginError }}
                            </p>
                        </div>
                        <form action="" @submit.prevent="login">
                            <div class="field">
                                <label for="" class="label">Email</label>
                                <div class="control has-icons-left">
                                    <input 
                                        type="email" 
                                        placeholder="e.g. bobsmith@gmail.com" 
                                        v-model="email" 
                                        class="input" 
                                        required>
                                    <span class="icon is-small is-left">
                                        <i class="fa fa-envelope"></i>
                                    </span>
                                </div>
                            </div>
                            <div class="field">
                                <label for="" class="label">Password</label>
                                <div class="control has-icons-left">
                                    <input 
                                        type="password" 
                                        placeholder="*******" 
                                        v-model="password" 
                                        class="input" 
                                        required>
                                    <span class="icon is-small is-left">
                                        <i class="fa fa-lock"></i>
                                    </span>
                                </div>
                            </div>
                            <div class="field-level" style="margin-bottom: 1em;">
                                <div class="field-body">
                                    <div class="field">
                                        <label for="" class="checkbox">
                                            <input type="checkbox">
                                            Remember me
                                        </label>
                                    </div>
                                    <div class="field">
                                        <p class="has-text-right"><Strong class="has-text-link">Forgot password?</Strong></p>
                                    </div>
                                </div>
                            </div>
                            <div class="field">
                                <input 
                                    type="submit" 
                                    value="Sign in to your account"
                                    class="button is-link is-fullwidth"
                                >
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
export default {
    name: "LoginView",
    data(){
        return {
            email: null,
            password: null
        }
    },
    methods: {
        login() {
            if(this.email && this.password) {
                this.$store.dispatch("login", {
                    email: this.email,
                    password: this.password
                });
            }
        }
    },
    computed: {
        loginError() {
            return this.$store.state.authentication.loginError;
        }
    }
}
</script>