import axios from "axios";

const API = "http://127.0.0.1:3000/api";

const admin = {
  state: {
    users: [],
    departments: []
  },
  mutations: {
    UPDATE_USERS_LIST(state, users) {
      state.users = users;
    },
    UPDATE_DEPARTMENTS_LIST(state, departments) {
      state.departments = departments;
    }
  },
  actions: {
    fetchStaff(context, token) {
      axios
        .get(API + "/users", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_USERS_LIST", response.data);
        });
    },
    fetchDepartments(context, token) {
      axios
        .get(API + "/departments", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_DEPARTMENTS_LIST", response.data);
        });
    }
  }
};

export default admin;
