import axios from "axios";
import jwt from "jsonwebtoken";
import router from "../router";
import { authenticate } from "../helpers/authentication";

const API = "http://127.0.0.1:3000/api";
const authentication = {
  state: {
    currentUser: null,
    inMemoryToken: null,
    authenticated: false,
    intervalID: null,
    loginError: null
  },
  mutations: {
    UPDATE_AUTHENTICATION_STATUS(state, status) {
      state.authenticated = status;
    },
    UPDATE_USER(state, user) {
      state.currentUser = user;
    },
    UPDATE_INMEMORY_TOKEN(state, token) {
      state.inMemoryToken = token;
    },
    UPDATE_INTERVAL(state, intervalID) {
      state.intervalID = intervalID;
    },
    UPDATE_LOGIN_ERROR(state, error) {
      state.loginError = error;
    }
  },
  actions: {
    login(context, userCredentials) {
      axios
        .post(API + "/login", userCredentials, { withCredentials: true })
        .then(response => {
          context.commit("UPDATE_AUTHENTICATION_STATUS", true);
          const user = jwt.decode(response.data.accessToken);
          context.commit("UPDATE_USER", user);
          context.commit("UPDATE_INMEMORY_TOKEN", response.data.accessToken);

          let intervalID = setInterval(() => {
            authenticate();
          }, 5000);
          context.commit("UPDATE_INTERVAL", intervalID);
          let hospital = user.hospital;
          let username = user.first_name + user.last_name;
          if (context.state.loginError) {
            context.commit("UPDATE_LOGIN_ERROR", null);
          }
          switch (user.role) {
            case "receptionist":
              router.push(`/${hospital}/receptionist/${username}/registration`);
              break;
            case "cashier":
              router.push(`/${hospital}/cashier/${username}/lab-requests`);
              break;
            case "labtech":
              router.push(`/${hospital}/labtech/${username}/exam-requests`);
              break;
            case "biologist":
              router.push(`/${hospital}/biologist/${username}`);
              break;
            case "admin":
              router.push(`/${hospital}/admin/${username}/users`);
              break;
            case "partner":
              router.push(`/${hospital}/partner/${username}/lab-requests`);
              break;
          }
        })
        .catch(() => {
          context.commit("UPDATE_LOGIN_ERROR", "Wrong email or password");
        });
    },
    resetCredentials(context, { user, token }) {
      context.commit("UPDATE_AUTHENTICATION_STATUS", true);
      context.commit("UPDATE_USER", user);
      context.commit("UPDATE_INMEMORY_TOKEN", token);
    },
    logout(context) {
      let API = "http://127.0.0.1:3000/api";
      let user = context.state.currentUser;
      let token = context.state.inMemoryToken;
      let intervalID = context.state.intervalID;
      axios
        .post(API + "/logout", user, {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(() => {
          clearInterval(intervalID);
          context.commit("UPDATE_AUTHENTICATION_STATUS", false);
          context.commit("UPDATE_USER", null);
          context.commit("UPDATE_INMEMORY_TOKEN", null);
          context.commit("UPDATE_INTERVAL", null);
          router.push("/");
        });
    }
  }
};

export default authentication;
