import axios from "axios";

const API = "http://127.0.0.1:3000/api";

const labtech = {
  state: {
    labRequests: [],
    exams: [],
    vaccines: [],
    isResultRegistrationModalOn: false,
    selectedRegistrationExamRequest: null,
    isCreateExamModalOn: false,
    isEditExamModalOn: false,
    activeExam: null,
    isCreateVaccineModalOn: false,
    isEditVaccineModalOn: false,
    activeVaccine: null
  },
  mutations: {
    UPDATE_LAB_REQUESTS(state, labRequests) {
      state.labRequests = labRequests;
    },
    ADD_PAID_LAB_REQUEST(state, labRequest) {
      state.labRequests.push(labRequest);
    },
    ADD_PAID_LAB_REQUESTS(state, labRequests) {
      let newlabRequests = state.labRequests.concat(labRequests);
      state.labRequests = newlabRequests;
    },
    UPDATE_EXAMS(state, exams) {
      state.exams = exams;
    },
    UPDATE_VACCINES(state, vaccines) {
      state.vaccines = vaccines;
    },
    TOGGLE_RESULT_REGISTRATION_MODAL(state, examRequest) {
      state.isResultRegistrationModalOn = examRequest;
    },
    UPDATE_SELECTED_REGISTRATION_EXAM_REQUEST(state, examRequest) {
      state.selectedRegistrationExamRequest = examRequest;
    },
    TOGGLE_CREATE_EXAM_MODAL_ON(state, status) {
      state.isCreateExamModalOn = status;
    },
    TOGGLE_EDIT_EXAM_MODAL_ON(state, status) {
      state.isEditExamModalOn = status;
    },
    UPDATE_ACTIVE_EXAM(state, exam) {
      state.activeExam = exam;
    },
    TOGGLE_CREATE_VACCINE_MODAL_ON(state, status) {
      state.isCreateVaccineModalOn = status;
    },
    TOGGLE_EDIT_VACCINE_MODAL_ON(state, status) {
      state.isEditVaccineModalOn = status;
    },
    UPDATE_ACTIVE_VACCINE(state, vaccine) {
      state.activeVaccine = vaccine;
    },
    ADD_CREATED_EXAM(state, exam) {
      state.exams.push(exam);
    },
    ADD_CREATED_VACCINE(state, vaccine) {
      state.vaccines.push(vaccine);
    }
  },
  actions: {
    addPaidLabRequest(context, labRequest) {
      context.commit("ADD_PAID_LAB_REQUEST", labRequest);
    },
    fetchLabTechLabRequests(context, token) {
      axios
        .get(API + "/labrequests?status=paid", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_LAB_REQUESTS", response.data);
          axios
            .get(API + "/labrequests?status=sample_collected", {
              withCredentials: true,
              headers: { Authorization: `Bearer ${token}` }
            })
            .then(function(response) {
              context.commit("ADD_PAID_LAB_REQUESTS", response.data);
            });
        });
    },
    fetchLabtechExams(context, token) {
      axios
        .get(API + "/exams", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_EXAMS", response.data);
        })
        .catch(() => context.commit("UPDATE_EXAMS", []));
    },
    fetchLabtechVaccines(context, token) {
      axios
        .get(API + "/vaccines", {
          withCredentials: true,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(function(response) {
          context.commit("UPDATE_VACCINES", response.data);
        })
        .catch(() => context.commit("UPDATE_VACCINES", []));
    },
    createVaccine(context, newVaccine) {
      let vaccine = newVaccine;
      vaccine.aliases = vaccine.aliases.map(alias => alias.value);
      vaccine.price = parseFloat(vaccine.price);
      vaccine.cost = vaccine.price;
      vaccine.discounts = vaccine.discounts.map(discount => {
        discount.value = parseFloat(discount.value);
        return discount;
      });
      vaccine.active_discounts = [];
      vaccine.is_administered = false;
      vaccine.created_at = Date.now();
      vaccine.updated_at = Date.now();
      context.commit("UPDATE_CREATED_VACCINE", vaccine);
    },
    toggleResultRegistrationModalOn(context, status) {
      context.commit("TOGGLE_RESULT_REGISTRATION_MODAL", status);
    },
    updateSelectedRegistrationExamRequest(context, labRequest) {
      context.commit("UPDATE_SELECTED_REGISTRATION_EXAM_REQUEST", labRequest);
    },
    toggleCreateExamModalOn(context, status) {
      context.commit("TOGGLE_CREATE_EXAM_MODAL_ON", status);
    },
    toggleEditExamModalOn(context, status) {
      context.commit("TOGGLE_EDIT_EXAM_MODAL_ON", status);
    },
    updateActiveExam(context, exam) {
      context.commit("UPDATE_ACTIVE_EXAM", exam);
    },
    toggleCreateVaccineModalOn(context, status) {
      context.commit("TOGGLE_CREATE_VACCINE_MODAL_ON", status);
    },
    toggleEditVaccineModalOn(context, status) {
      context.commit("TOGGLE_EDIT_VACCINE_MODAL_ON", status);
    },
    updateActiveVaccine(context, vaccine) {
      context.commit("UPDATE_ACTIVE_VACCINE", vaccine);
    },
    addCreatedExam(context, exam) {
      context.commit("ADD_CREATED_EXAM", exam);
    },
    addCreatedVaccine(context, vaccine) {
      context.commit("ADD_CREATED_VACCINE", vaccine);
    }
  },
  getters: {
    getIncompleteExamRequests(state) {
      return state.labRequests.filter(labRequest => {
        return (
          labRequest.status === "paid" ||
          labRequest.status === "sample_collected"
        );
      });
    }
  }
};

export default labtech;
