<style scoped>

@media (max-width: 642px) {
    .field.is-grouped > .control:not(:last-child) {
        margin-right: 0px;
    }
}
</style>

<template>
  <div class="modal" :class="{'is-active': isActive }">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head has-background-link">
        <p class="modal-card-title has-text-white" style="margin-bottom: 0em;">Request a demo</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="field is-grouped is-grouped-multiline">
          <div class="control is-expanded">
            <label class="label">First name</label>
            <input
              class="input is-hovered"
              type="text"
              v-model="fields.firstName"
            />
          </div>
          <div class="control is-expanded">
            <label class="label">Last name</label>
            <input
              class="input is-hovered"
              type="text"
              v-model="fields.lastName"
            />
          </div>
        </div>
        <div class="field">
          <label class="label">Email</label>
          <div class="control">
            <input class="input is-hovered" type="email" v-model="fields.email" />
          </div>
        </div>
        <div class="field">
          <label class="label">Telephone</label>
          <div class="control">
            <input
              class="input is-hovered"
              type="telephone"
              placeholder="e.g +2547123456789"
              v-model="fields.telephone"
            />
          </div>
        </div>
        <div class="field">
          <div class="control">
            <label class="label">Laboratory name</label>
            <input
              class="input is-hovered"
              type="text"
              v-model="fields.laboratory"
            />
          </div>
        </div>
      </section>
      <footer class="modal-card-foot">
        <button class="button is-link is-fullwidth is-medium" @click="saveData">Submit request</button>
      </footer>
    </div>
  </div>
</template>


<script>
import axios from "axios";

export default {
  name: "requestdemo",
  data() {
    return {
      fields: {
        firstName: "",
        lastName: "",
        email: "",
        telephone: "",
        email: "",
        laboratory: ""
      }
    };
  },
  props: ["isActive"],
  methods: {
    closeModal() {
      this.$emit("close");
    },
    saveData() {
      axios({
        method: "get",
        url:
          "https://script.google.com/macros/s/AKfycbzoh2JYTlCNZGORRTMWMVxaFjggIhMIliwwXuMXtmAIXU3pKl0/exec",
        params: {
          firstName: this.fields.firstName,
          lastName: this.fields.lastName,
          email: this.fields.email,
          telephone: this.fields.telephone,
          laboratory: this.fields.laboratory
        }
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.error(err);
        });

      this.$emit("close");
    }
  }
};
</script>